@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&family=JetBrains+Mono:wght@600&display=swap);
.constructor{display:flex;flex-direction:row}.page{overflow-y:auto;height:100%}html{overflow:hidden}::-webkit-scrollbar{width:2px;height:2px}::-webkit-scrollbar-button{width:0px;height:0px}::-webkit-scrollbar-thumb{background:#e1e1e1;border:0px none #ffffff;border-radius:50px}::-webkit-scrollbar-thumb:hover{background:#ffffff}::-webkit-scrollbar-thumb:active{background:#000000}::-webkit-scrollbar-track{background:#000000;border:0px none #ffffff;border-radius:50px}::-webkit-scrollbar-track:hover{background:#666666}::-webkit-scrollbar-track:active{background:#333333}::-webkit-scrollbar-corner{background:transparent}#root{height:100vh;width:100vw}

.scene{position:relative;width:100%;height:100%;overflow:hidden;display:flex;justify-content:center}.scene .background{position:absolute;width:100%;height:100%;object-fit:contain}.scene .background img{width:100%;height:100%}.scene .person{position:absolute;height:100%;left:10px;bottom:-50px}.scene .person img{height:100%}.scene .controls-wrap{position:absolute;display:flex;flex-direction:column;bottom:0;margin:20px;width:85%;-webkit-filter:FlipH;filter:FlipH;-ms-filter:"FlipH"}.scene .controls-wrap .name{color:#E9F3FF;font-weight:900;font-size:20px;margin-bottom:20px;letter-spacing:2px;padding:20px;background-repeat:no-repeat;background-size:auto;background-image:url(/static/media/nameBubble.5c66bdaa.svg);font-family:'JetBrains Mono', monospace;font-weight:700;text-shadow:1px 1px 7px #04E2FC}.scene .controls-wrap .name-earth{text-shadow:1px 1px 7px #04E2FC;color:#E9F3FF;line-height:0;font-size:32px}.scene .controls-wrap .controls{position:relative;display:flex;flex-direction:column;justify-content:space-between;background-repeat:no-repeat;background-size:cover;background-position:center center;padding:20px;height:400px;background-image:url(/static/media/dialogBackground.b2a29c40.svg)}.scene .controls-wrap .controls .dialog{font-family:'Inter', sans-serif;position:relative;white-space:break-spaces;color:#FFF;font-weight:500;font-size:16px;margin:20px;max-width:800px;max-height:200px;overflow-y:auto}.scene .controls-wrap .controls .buttons{padding:0 20px;display:flex;flex-direction:column}.scene .controls-wrap .controls .buttons .button{position:relative;background-image:url(/static/media/button.51e4bce9.svg);background-color:transparent;background-repeat:no-repeat;background-size:cover;max-width:300px;border:none;font-family:'Inter', sans-serif;font-weight:600;padding:8px 15px;letter-spacing:0.75px;color:#FBF8FF;font-size:18px;letter-spacing:0.75px;cursor:pointer;margin-bottom:15px;transition:0.3s;text-align:left}.config-buttons{position:absolute;top:10px;z-index:3}.scene-form{position:relative;padding:50px 20px}


.layout{height:100%}.layout .footer{background:white;border-top:1px solid #eee;padding:5px 10px}

.page-constructor{display:flex;flex-direction:row;justify-content:space-between;width:100%;overflow:hidden}.page-constructor .page-scenes{width:50%;overflow:auto}.page-constructor .page-map{width:50%;overflow:hidden}

.page-media{margin:20px}.page-media .media-dnd{height:200px}.page-media .media-gallery{display:flex;flex-wrap:wrap;margin:10px -10px}.page-media .media-gallery .media-item{padding:10px}.page-media .media-gallery .media-item .media-image{max-height:300px;object-fit:contain}

.page-quests{justify-content:center;align-items:center}.page-quests .quest{width:400px;margin:20px}

